import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2 } from 'lucide-react';

interface EtisalatTransferFlowProps {
  isOpen: boolean;
  onClose: () => void;
  senderPhone: string;
  receiverPhone: string;
  amount: number;
  deviceId: string;
  api: any;
}

export const EtisalatTransferFlow: React.FC<EtisalatTransferFlowProps> = ({
  isOpen, onClose, senderPhone, receiverPhone, amount, deviceId, api
}) => {
  const { toast } = useToast();
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [pin, setPin] = useState('');

  useEffect(() => {
    if (isOpen) {
      setStep(1);
      setPin('');
    }
  }, [isOpen]);

  const sendToDevice = async (text: string) => {
    try {
      if (api && typeof api.enterPin === 'function') {
        await api.enterPin(deviceId, text);
        return true;
      }
      return false;
    } catch (e: any) {
      console.error('ADB error:', e);
      toast({ title: ' ADB', description: e.message || ' ', variant: 'destructive' });
      return false;
    }
  };

  const handleStep1 = async () => {
    if (!pin) {
      toast({ title: '  ', variant: 'destructive' });
      return;
    }
    setLoading(true);
    const ok = await sendToDevice(pin);
    setLoading(false);
    if (ok) {
      setStep(2);
    }
  };

  const handleStep2 = async () => {
    setLoading(true);
    const ok = await sendToDevice(String(amount));
    setLoading(false);
    if (ok) {
      setStep(3);
    }
  };

  const handleStep3 = async () => {
    setLoading(true);
    const ok = await sendToDevice(receiverPhone);
    setLoading(false);
    if (ok) {
      setStep(4);
    }
  };

  const handleStep4 = async () => {
    setLoading(true);
    const ok = await sendToDevice(receiverPhone);
    setLoading(false);
    if (ok) {
      setStep(5);
    }
  };

  const handleStep5 = async () => {
    setLoading(true);
    const ok = await sendToDevice('1');
    setLoading(false);
    if (ok) {
      toast({ title: '  ', description: '  ' });
      onClose();
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onClose()}>
      <DialogContent className="sm:max-w-[400px]">
        <DialogHeader>
          <DialogTitle>   (USSD)</DialogTitle>
          <DialogDescription>
            {step === 1 && ' 1:    '}
            {step === 2 && ' 2:   '}
            {step === 3 && ' 3:   '}
            {step === 4 && ' 4:   '}
            {step === 5 && ' 5:   '}
          </DialogDescription>
        </DialogHeader>

        <div className="py-4 space-y-4">
          {step === 1 && (
            <div className="space-y-2">
                <label className="text-sm font-medium">  (PIN)</label>
                <Input 
                    type="password" 
                    value={pin} 
                    onChange={(e) => setPin(e.target.value)}
                    placeholder="****" 
                    autoFocus
                    className="text-center text-lg tracking-widest"
                    maxLength={6}
                    onKeyDown={(e) => {
                        if (e.key === 'Enter' && pin) handleStep1();
                    }}
                />
            </div>
          )}

          {step === 2 && (
             <div className="text-center bg-gray-50 dark:bg-slate-800 p-4 rounded-lg">
                <p className="text-sm text-gray-500 dark:text-gray-400 mb-2">  :</p>
                <p className="text-2xl font-bold font-mono tracking-wider">{amount}</p>
             </div>
          )}

          {step === 3 && (
             <div className="text-center bg-gray-50 dark:bg-slate-800 p-4 rounded-lg">
                <p className="text-sm text-gray-500 dark:text-gray-400 mb-2">   :</p>
                <p className="text-2xl font-bold font-mono tracking-wider">{receiverPhone}</p>
             </div>
          )}

          {step === 4 && (
             <div className="text-center bg-gray-50 dark:bg-slate-800 p-4 rounded-lg">
                <p className="text-sm text-gray-500 dark:text-gray-400 mb-2">   :</p>
                <p className="text-2xl font-bold font-mono tracking-wider">{receiverPhone}</p>
             </div>
          )}

          {step === 5 && (
             <div className="text-center bg-green-50 dark:bg-green-900/20 p-4 rounded-lg border border-green-100 dark:border-green-900">
                <p className="text-lg font-bold text-green-700 dark:text-green-400 mb-2"> </p>
                <p className="text-sm text-gray-600 dark:text-gray-300">   1  </p>
             </div>
          )}
        </div>

        <DialogFooter>
          {step === 1 && (
            <Button onClick={handleStep1} disabled={loading || !pin} className="w-full bg-black hover:bg-neutral-800 text-white">
              {loading ? <Loader2 className="animate-spin h-4 w-4 mr-2" /> : '  '}
            </Button>
          )}
          {step === 2 && (
            <Button onClick={handleStep2} disabled={loading} className="w-full bg-black hover:bg-neutral-800 text-white">
              {loading ? <Loader2 className="animate-spin h-4 w-4 mr-2" /> : ' '}
            </Button>
          )}
          {step === 3 && (
            <Button onClick={handleStep3} disabled={loading} className="w-full bg-black hover:bg-neutral-800 text-white">
              {loading ? <Loader2 className="animate-spin h-4 w-4 mr-2" /> : ' '}
            </Button>
          )}
          {step === 4 && (
            <Button onClick={handleStep4} disabled={loading} className="w-full bg-black hover:bg-neutral-800 text-white">
               {loading ? <Loader2 className="animate-spin h-4 w-4 mr-2" /> : ' '}
            </Button>
          )}
          {step === 5 && (
            <Button onClick={handleStep5} disabled={loading} className="w-full bg-green-600 hover:bg-green-700 text-white">
               {loading ? <Loader2 className="animate-spin h-4 w-4 mr-2" /> : ' (1)'}
            </Button>
          )}
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};
