import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { useToast } from '@/hooks/use-toast';
import { db } from '@/firebase';
import { doc, serverTimestamp, collection, getDoc, writeBatch, increment } from 'firebase/firestore';
import { Loader2, ArrowRight, PlayCircle } from 'lucide-react';
import { walletDailyLimitsService } from '@/utils/walletDailyLimitsService';
import { ProfitService } from '@/utils/profitService';
import { VideoModal } from '@/components/VideoModal';

interface Wallet {
  id: string;
  name: string;
  phone?: string;
  balance?: number;
}

interface AtmWithdrawDialogProps {
  isOpen: boolean;
  onClose: () => void;
  userId: string;
  wallets: any[];
  walletBalances: Record<string, number>;
  onSuccess?: () => void;
}

export const AtmWithdrawDialog: React.FC<AtmWithdrawDialogProps> = ({
  isOpen,
  onClose,
  userId,
  wallets,
  walletBalances,
  onSuccess
}) => {
  const { toast } = useToast();
  const [step, setStep] = useState<1 | 2>(1);
  
  // Step 1 State
  const [amount, setAmount] = useState<string>('');
  const [commission, setCommission] = useState<string>('');
  const [sourceWalletId, setSourceWalletId] = useState<string>('');

  // Step 2 State
  const [destinationType, setDestinationType] = useState<'cash' | 'wallet' | 'none'>('cash');
  const [destinationWalletId, setDestinationWalletId] = useState<string>('');
  
  const [isProcessing, setIsProcessing] = useState(false);
  const [isVideoModalOpen, setIsVideoModalOpen] = useState(false);

  // Reset state when dialog opens
  useEffect(() => {
    if (isOpen) {
      setStep(1);
      setAmount('');
      setCommission('');
      setSourceWalletId('');
      setDestinationType('cash');
      setDestinationWalletId('');
    }
  }, [isOpen]);

  const handleNextStep = () => {
    if (!amount || parseFloat(amount) <= 0) {
      toast({ title: "", description: "   ", variant: "destructive" });
      return;
    }
    if (!sourceWalletId) {
      toast({ title: "", description: "  ", variant: "destructive" });
      return;
    }
    
    // Check balance (UI check only)
    const currentBalance = walletBalances[sourceWalletId] || 0;
    if (parseFloat(amount) > currentBalance) {
      toast({ title: "", description: "   ", variant: "destructive" });
      return;
    }

    setStep(2);
  };

  const handleConfirm = async () => {
    if (destinationType === 'wallet' && !destinationWalletId) {
      toast({ title: "", description: "   ", variant: "destructive" });
      return;
    }

    setIsProcessing(true);
    try {
      const numericAmount = parseFloat(amount);
      const numericCommission = commission ? parseFloat(commission) : 0;
      // Logic Update: Deduct (Amount + Commission) from Source, Add (Amount) to Destination
      const totalDeductedFromWallet = numericAmount + numericCommission;
      const amountAddedToDestination = numericAmount;

      // 1. Get User Document for Balance Check and Updates
      const userRef = doc(db, 'users', userId);
      const userDoc = await getDoc(userRef);
      
      if (!userDoc.exists()) throw "User document not found";

      const userData = userDoc.data();
      const userWalletBalances = userData.walletBalances || {};
      
      // Check balance in user document (Source of Truth for Dashboard)
      const currentSourceBalance = Number(userWalletBalances[sourceWalletId] || 0);

      console.log('[AtmWithdrawDialog] Validation:', {
        sourceWalletId,
        currentSourceBalance,
        numericAmount,
        numericCommission,
        totalDeductedFromWallet
      });

      if (currentSourceBalance < totalDeductedFromWallet) {
        throw `    (: ${currentSourceBalance},  : ${totalDeductedFromWallet})`;
      }

      // Check Transfer Limits
      const limitCheck = await walletDailyLimitsService.canPerformOperation(sourceWalletId, numericAmount, 'transfer');
      if (!limitCheck.canPerform) {
        throw limitCheck.message || "   ";
      }

      // --- Batch Writes ---
      const batch = writeBatch(db);

      // 4. Update Source (Decrement Total = Amount + Commission)
      const updates: any = {};
      updates[`walletBalances.${sourceWalletId}`] = increment(-totalDeductedFromWallet);
      
      // 5. Update Destination (Increment Amount only)
      let destinationDetails = "";
      if (destinationType === 'wallet' && destinationWalletId) {
          // Update Destination in User Doc Map
          updates[`walletBalances.${destinationWalletId}`] = increment(amountAddedToDestination);
          destinationDetails = `Wallet: ${wallets.find(w => w.id === destinationWalletId)?.name}`;
      } else if (destinationType === 'cash') {
          // Update Cash Balance in User Doc
          updates['cashBalance'] = increment(amountAddedToDestination);
          destinationDetails = "Cash Drawer";
      } else {
          destinationDetails = "No Addition";
      }
      
      // Apply updates to User Document
      batch.update(userRef, updates);

      // 6. Record Transaction
      const sourceWallet = wallets.find(w => w.id === sourceWalletId);
      const userTxRef = doc(collection(db, 'userTransactions'));
      batch.set(userTxRef, {
        userId,
        type: 'atm_withdraw',
        amount: numericAmount,
        commission: numericCommission,
        totalCharged: totalDeductedFromWallet, // New field for clarity
        netAmount: amountAddedToDestination,   // Now this is the amount added
        sourceWalletId,
        destinationType,
        destinationWalletId: destinationType === 'wallet' ? destinationWalletId : null,
        status: 'completed',
        createdAt: serverTimestamp(),
        note: ` ATM  ${sourceWallet?.name}`,
        walletId: sourceWalletId,
        sender_msisdn: sourceWallet?.phone || '', 
        details: `ATM Withdraw from ${sourceWallet?.name} -> ${destinationDetails}`
      });

      await batch.commit();

      // Update Transfer Limits
      try {
        await walletDailyLimitsService.updateUsage(sourceWalletId, numericAmount, 'transfer');
      } catch (limitError) {
        console.error("Failed to update wallet limits:", limitError);
      }

      //      
      if (numericCommission > 0) {
        try {
          ProfitService.addProfit(-numericCommission, userId);
        } catch (e) {
          console.error("Failed to update profits:", e);
        }
      }

      // Dispatch event to update dashboard state immediately
      let eventDetail: any = {};
    
      if (destinationType === 'cash') {
          // Use delta for Cash + Source Wallet update
          eventDetail = {
              delta: {
                  amount: amountAddedToDestination,
                  commission: numericCommission,
                  totalDeducted: totalDeductedFromWallet,
                  type: 'atm_withdraw',
                  walletId: sourceWalletId
              }
          };
      } else {
          // Use walletBalances override for Wallet -> Wallet or Wallet -> None
          const updatedWallets = { ...walletBalances };
          // Update Source
          updatedWallets[sourceWalletId] = (updatedWallets[sourceWalletId] || 0) - totalDeductedFromWallet;
          // Update Dest
          if (destinationType === 'wallet') {
              updatedWallets[destinationWalletId] = (updatedWallets[destinationWalletId] || 0) + amountAddedToDestination;
          }
          eventDetail = { walletBalances: updatedWallets };
      }
      
      window.dispatchEvent(new CustomEvent('dashboard:external-update', { detail: eventDetail }));

      toast({ title: "", description: "   " });
      onSuccess?.();
      onClose();
    } catch (error) {
      console.error("ATM Withdraw Error:", error);
      toast({ title: "", description: typeof error === 'string' ? error : "   ", variant: "destructive" });
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
             ATM
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setIsVideoModalOpen(true)}
              className="gap-1 px-2 text-rose-600 hover:text-rose-700 hover:bg-rose-50 mx-2"
            >
              <PlayCircle className="h-4 w-4" />
              <span className="text-xs font-medium">  atm</span>
            </Button>
          </DialogTitle>
          <DialogDescription>
            {step === 1 ? "   " : "   "}
          </DialogDescription>
        </DialogHeader>

        {step === 1 ? (
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label> </Label>
              <Input
                type="number"
                placeholder=" "
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
            </div>
            
            <div className="space-y-2">
              <Label>  ()</Label>
              <Input
                type="number"
                placeholder=" "
                value={commission}
                onChange={(e) => setCommission(e.target.value)}
              />
            </div>

            <div className="space-y-2">
              <Label>   </Label>
              <Select value={sourceWalletId} onValueChange={setSourceWalletId}>
                <SelectTrigger>
                  <SelectValue placeholder=" " />
                </SelectTrigger>
                <SelectContent>
                  {wallets.map((wallet) => (
                    <SelectItem key={wallet.id} value={wallet.id}>
                      <div className="flex justify-between w-full gap-4">
                        <span>{wallet.phone || wallet.name}</span>
                        <span className="text-muted-foreground text-xs">
                          {walletBalances[wallet.id] 
                            ? `${Number(walletBalances[wallet.id]).toLocaleString('en-US', { maximumFractionDigits: 0 })} .` 
                            : '0 .'}
                        </span>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
        ) : (
          <div className="space-y-4 py-4">
            <div className="bg-muted p-4 rounded-lg mb-4">
              <div className="flex justify-between mb-2">
                 <span className="text-sm text-muted-foreground">:</span>
                 <span className="font-bold">{amount ? Number(amount).toLocaleString('en-US', { maximumFractionDigits: 0 }) : 0} .</span>
               </div>
               <div className="flex justify-between mb-2">
                 <span className="text-sm text-muted-foreground"> ( ):</span>
                 <span className="font-bold text-red-500">{commission ? Number(commission).toLocaleString('en-US', { maximumFractionDigits: 0 }) : 0} .</span>
               </div>
               {(parseFloat(commission) > 0) && (
                 <div className="text-[10px] text-amber-600 mb-2 text-right">
                   *       
                 </div>
               )}
               <div className="flex justify-between mb-2 pt-2 border-t border-dashed">
                 <span className="text-sm text-muted-foreground">   :</span>
                 <span className="font-bold text-red-600">{(Number(amount) + (Number(commission) || 0)).toLocaleString('en-US', { maximumFractionDigits: 0 })} .</span>
               </div>
               <div className="border-t pt-2 flex justify-between">
                 <span className="text-sm font-semibold">  :</span>
                 <span className="font-bold text-green-600">{amount ? Number(amount).toLocaleString('en-US', { maximumFractionDigits: 0 }) : 0} .</span>
               </div>
            </div>

            <div className="space-y-3">
              <Label>  </Label>
              <RadioGroup value={destinationType} onValueChange={(v: any) => setDestinationType(v)} className="flex flex-col gap-2">
                <div className="flex items-center space-x-2 space-x-reverse">
                  <RadioGroupItem value="cash" id="r1" />
                  <Label htmlFor="r1" className="cursor-pointer">  (Cash Drawer)</Label>
                </div>
                <div className="flex items-center space-x-2 space-x-reverse">
                  <RadioGroupItem value="wallet" id="r2" />
                  <Label htmlFor="r2" className="cursor-pointer"> </Label>
                </div>
                <div className="flex items-center space-x-2 space-x-reverse">
                  <RadioGroupItem value="none" id="r3" />
                  <Label htmlFor="r3" className="cursor-pointer">  ( )</Label>
                </div>
              </RadioGroup>
            </div>

            {destinationType === 'wallet' && (
              <div className="space-y-2 mt-4 animate-in fade-in slide-in-from-top-2">
                <Label>  </Label>
                <Select value={destinationWalletId} onValueChange={setDestinationWalletId}>
                  <SelectTrigger>
                    <SelectValue placeholder=" " />
                  </SelectTrigger>
                  <SelectContent>
                    {wallets.filter(w => w.id !== sourceWalletId).map((wallet) => (
                      <SelectItem key={wallet.id} value={wallet.id}>
                        {wallet.phone || wallet.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            )}
          </div>
        )}

        <DialogFooter className="flex-col sm:flex-row gap-2">
          {step === 2 && (
            <Button variant="outline" onClick={() => setStep(1)} disabled={isProcessing}>
              
            </Button>
          )}
          
          {step === 1 ? (
            <Button onClick={handleNextStep} className="w-full sm:w-auto bg-blue-600 hover:bg-blue-700 text-white">
               <ArrowRight className="mr-2 h-4 w-4" />
            </Button>
          ) : (
            <Button onClick={handleConfirm} disabled={isProcessing} className="w-full sm:w-auto bg-green-600 hover:bg-green-700">
              {isProcessing && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
               
            </Button>
          )}
        </DialogFooter>
      </DialogContent>
      <VideoModal
        isOpen={isVideoModalOpen}
        onClose={() => setIsVideoModalOpen(false)}
        videoUrl="https://www.youtube.com/watch?v=V_wM36ICBck"
        title="  ATM"
        className="z-[10002]"
      />
    </Dialog>
  );
};
