import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, DollarSign, ArrowDownLeft, ArrowRightLeft, Clock, Package } from 'lucide-react';
import { ProfitService } from '@/utils/profitService';
import { ProfitPinDialog } from './ProfitPinDialog';
import { formatCurrency as fmtCurrencyUtil } from '@/utils/formatters';
import { reportsService } from '@/utils/reportsService';
import { db } from '@/firebase';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { useShop } from '@/hooks/useShop';

interface DailyProfitDisplayDialogProps {
  userId?: string;
  transactions: any[];
}

interface ProfitData {
  dailyWithdrawalProfit: number;
  dailyTransferProfit: number;
  dailySalesProfit: number;
  lastUpdated: Date;
}

const DailyProfitDisplayDialog: React.FC<DailyProfitDisplayDialogProps> = ({ userId, transactions }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isPinDialogOpen, setIsPinDialogOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [profitData, setProfitData] = useState<ProfitData>({
    dailyWithdrawalProfit: 0,
    dailyTransferProfit: 0,
    dailySalesProfit: 0,
    lastUpdated: new Date()
  });
  const { shopId } = useShop();

  useEffect(() => {
    if (userId && isOpen && isAuthenticated) {
      loadProfitData();
    }
  }, [userId, isOpen, isAuthenticated, transactions, shopId]);

  //      (    )   
  useEffect(() => {
    if (!isOpen || !isAuthenticated) return;
    const interval = setInterval(() => {
      const now = new Date();
      if (profitData.lastUpdated.getDate() !== now.getDate()) {
        loadProfitData();
      }
    }, 30000); //   30 
    return () => clearInterval(interval);
  }, [isOpen, isAuthenticated, profitData.lastUpdated]);

  const fetchTodaySalesProfit = async (): Promise<number> => {
    if (!userId) return 0;
    
    //      
    const formatter = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' });
    const todayCairoStr = formatter.format(new Date());
    
    let salesProfit = 0;

    try {
      //       
      const qMain = query(
        collection(db, 'dailySales'),
        where('userId', '==', userId),
        ...(shopId ? [where('shopId', '==', shopId)] : [])
      );
      const snapMain = await getDocs(qMain);
      const combinedDocs: { id: string; data: any }[] = [];
      snapMain.forEach(d => {
        combinedDocs.push({ id: d.id, data: d.data() });
      });

      //  :    
      const snapUser = await getDocs(collection(db, 'users', userId, 'dailySales'));
      snapUser.forEach((d) => {
        const data = d.data();
        if (shopId && data.shopId && data.shopId !== shopId) return;
        combinedDocs.push({ id: d.id, data });
      });

      const uniqueDocs = new Map<string, any>();
      combinedDocs.forEach(item => {
        const key = String(item.data.clientId || item.id);
        if (!uniqueDocs.has(key)) {
          uniqueDocs.set(key, item.data);
        }
      });

      uniqueDocs.forEach((data: any) => {
        if (data.isDeferred) return;

        let txDate: Date | null = null;
        if (data.createdAt) {
          if (typeof data.createdAt.toDate === 'function') txDate = data.createdAt.toDate();
          else if (data.createdAt instanceof Date) txDate = data.createdAt;
          else if (typeof data.createdAt === 'number') txDate = new Date(data.createdAt);
          else if (typeof data.createdAt === 'string') txDate = new Date(data.createdAt);
        } else if (data.date) {
          const timeStr = data.time || '00:00';
          txDate = new Date(`${data.date}T${timeStr}:00`);
        }

        let isToday = false;
        if (txDate && !isNaN(txDate.getTime())) {
          const txCairoStr = formatter.format(txDate);
          if (txCairoStr === todayCairoStr) isToday = true;
        } else {
          const dateStr = String(data.date || '').slice(0, 10);
          if (dateStr === todayCairoStr) isToday = true;
        }

        if (isToday) {
          const qty = Number(data.quantity || 1);
          const pr = Number(
            data.profit ?? ((Number(data.sellingPrice || 0) - Number(data.wholesalePrice || 0)) * qty)
          );
          salesProfit += pr;
        }
      });
    } catch (e) {
      console.warn('    :', e);
    }
    return Math.round(salesProfit * 100) / 100;
  };

  const loadProfitData = async () => {
    if (!userId) return;
    try {
      //            localStorage
      if (Array.isArray(transactions) && transactions.length > 0) {
        const today = new Date();
        const visibleDaily = reportsService.filterVisibleTransactions(transactions as any[], 'daily', userId);

        let dailyWithdrawProfit = 0;
        let dailyTransferProfit = 0;

        visibleDaily.forEach((tx: any) => {
          const txDate = new Date(tx.createdAt);
          const status = String(tx.status || 'confirmed').toLowerCase();
          if (txDate.toDateString() !== today.toDateString()) return;
          //    (pending)    
          const t = (tx.type || tx.txnType || '').toLowerCase();
          const title = String(tx.title || '');
          if (t === 'cash_addition' || title.includes('  ')) return;
          const txForCalc = {
            type: t === 'withdrawal' || t === 'machine_withdrawal' ? 'withdraw' : t,
            amount: Number(tx.amount || 0),
            commission: tx.commission,
          };

          if (txForCalc.type === 'withdraw' || txForCalc.type === 'atm_withdraw') {
            dailyWithdrawProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          } else if (txForCalc.type === 'send' || txForCalc.type === 'receive' || txForCalc.type === 'transfer') {
            dailyTransferProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          }
        });

        const dailySalesProfit = await fetchTodaySalesProfit();

        setProfitData({
          dailyWithdrawalProfit: Math.round(dailyWithdrawProfit * 100) / 100,
          dailyTransferProfit: Math.round(dailyTransferProfit * 100) / 100,
          dailySalesProfit,
          lastUpdated: new Date()
        });

        //         
        try { ProfitService.updateProfitsFromTransactions(transactions, userId); } catch {}
        return;
      }

      //        localStorage ()
      const profits = ProfitService.getWithdrawTransferProfits(userId);
      const dailySalesProfit = await fetchTodaySalesProfit();
      setProfitData({
        dailyWithdrawalProfit: profits.dailyWithdrawProfit || 0,
        dailyTransferProfit: profits.dailyTransferProfit || 0,
        dailySalesProfit,
        lastUpdated: new Date()
      });
    } catch (error) {
      console.error('Error loading profit data:', error);
    }
  };

  const formatCurrency = (amount: number) => {
    return `${Number(amount).toLocaleString('en-US', { maximumFractionDigits: 0 })} .`;
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  const handleEyeClick = () => {
    if (ProfitService.hasProfitPin(userId)) {
      setIsPinDialogOpen(true);
    } else {
      setIsPinDialogOpen(true);
    }
  };

  const handlePinSuccess = () => {
    setIsAuthenticated(true);
    setIsOpen(true);
    setIsPinDialogOpen(false);
  };

  return (
    <>
      <Button
        size="sm"
        variant="ghost"
        onClick={handleEyeClick}
        className="h-6 w-6 p-0 text-gray-400 hover:text-blue-600 hover:bg-blue-50 rounded-full transition-colors duration-200"
        title="  "
      >
        <Eye className="h-4 w-4" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-[280px] sm:max-w-[320px] mx-auto bg-white dark:bg-[#0f172a] border border-gray-200 dark:border-slate-800 shadow-xl rounded-xl p-0 overflow-hidden">
          <DialogHeader className="bg-gray-50 dark:bg-slate-900/50 px-4 py-3 border-b border-gray-100 dark:border-slate-800">
            <DialogTitle className="text-sm font-bold text-gray-900 dark:text-slate-100 flex items-center justify-center gap-2">
              <div className="p-1.5 bg-white dark:bg-slate-800 rounded-full border border-gray-200 dark:border-slate-700">
                <DollarSign className="h-4 w-4 text-blue-600 dark:text-blue-400" />
              </div>
               
            </DialogTitle>
          </DialogHeader>

          <div className="p-4 space-y-3">
            {/*  */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-red-50 dark:bg-red-900/20 rounded-md">
                  <ArrowDownLeft className="h-3.5 w-3.5 text-red-600 dark:text-red-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"></span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.dailyWithdrawalProfit)}
              </span>
            </div>

            {/*  */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-green-50 dark:bg-green-900/20 rounded-md">
                  <ArrowRightLeft className="h-3.5 w-3.5 text-green-600 dark:text-green-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"></span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.dailyTransferProfit)}
              </span>
            </div>

            {/*   */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-violet-50 dark:bg-violet-900/20 rounded-md">
                  <Package className="h-3.5 w-3.5 text-violet-600 dark:text-violet-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"> </span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.dailySalesProfit)}
              </span>
            </div>

            {/*  */}
            <div className="flex items-center justify-between p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg border border-blue-100 dark:border-blue-900/30 mt-2">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-white dark:bg-slate-800 rounded-md shadow-sm">
                  <DollarSign className="h-3.5 w-3.5 text-blue-600 dark:text-blue-400" />
                </div>
                <span className="font-bold text-blue-900 dark:text-blue-100 text-sm"></span>
              </div>
              <span className="font-bold text-base text-blue-700 dark:text-blue-300">
                {formatCurrency(profitData.dailyWithdrawalProfit + profitData.dailyTransferProfit + profitData.dailySalesProfit)}
              </span>
            </div>
          </div>

          <div className="bg-gray-50 dark:bg-slate-900/50 px-4 py-3 border-t border-gray-100 dark:border-slate-800 flex justify-center">
            <Button
              onClick={() => setIsOpen(false)}
              className="bg-white dark:bg-slate-800 border border-gray-300 dark:border-slate-700 text-gray-700 dark:text-slate-200 hover:bg-gray-50 dark:hover:bg-slate-700 hover:text-gray-900 w-full sm:w-auto px-8 rounded-lg text-sm font-medium shadow-sm"
            >
              
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <ProfitPinDialog
        open={isPinDialogOpen}
        onOpenChange={setIsPinDialogOpen}
        onSuccess={handlePinSuccess}
        userId={userId}
      />
    </>
  );
};

export const DailyProfitEyeButton: React.FC<DailyProfitDisplayDialogProps> = (props) => {
  return <DailyProfitDisplayDialog {...props} />;
};

export default DailyProfitDisplayDialog;
